.media {
}

.media__caption {
  font-size: $typescale-2;
  padding-top: $spacing-2;
  font-weight: $font-weight-normal;
  color: $gray-dark;
  line-height: 1.8;
  padding-left: 1rem;
  padding-right: 1rem;

  @include breakpoint(sm) {
    padding-left: 0;
    padding-right: 0;
  }

  a {
    @include link-border;
  }
}

.media--contain-caption {
  display: table;

  @include ie {
    table-layout: fixed;
    width: 100%;

    // Unset 100% width for floated images and media-objects
    // otherwise it causes layout issues in ie11
    .image--left &,
    .image--right &,
    .media-object & {
      width: auto;
    }
  }

  .media__caption {
    display: table-caption;
    caption-side: bottom;
  }
}
